"use client"
import { useState } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Send } from "lucide-react";

export default function ChatGPTClone() {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");

  const sendMessage = async () => {
    if (!input.trim()) return;
    const newMessage = { role: "user", content: input };
    setMessages([...messages, newMessage]);
    setInput("");

    const response = await fetch("/api/chat", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ messages: [...messages, newMessage] })
    });
    const data = await response.json();
    setMessages([...messages, newMessage, { role: "ai", content: data.reply }]);
  };

  return (
    <div className="flex flex-col h-screen max-w-2xl mx-auto p-4">
      <h1 className="text-xl font-bold text-center mb-4">ChatGPT Clone</h1>
      <div className="flex-1 overflow-auto space-y-2">
        {messages.map((msg, idx) => (
          <Card key={idx} className={msg.role === "user" ? "bg-gray-200" : "bg-blue-100"}>
            <CardContent className="p-3">{msg.content}</CardContent>
          </Card>
        ))}
      </div>
      <div className="flex gap-2 mt-4">
        <Input
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Type a message..."
        />
        <Button onClick={sendMessage} className="p-2">
          <Send size={18} />
        </Button>
      </div>
    </div>
  );
}
